import React, { Component, useRef, useEffect } from "react";
import ChartistGraph from "react-chartist";
import { Grid, Row, Col } from "react-bootstrap";

import { Card } from "components/Card/Card.jsx";
import { StatsCard } from "components/StatsCard/StatsCard.jsx";
import { Tasks } from "components/Tasks/Tasks.jsx";
import Tag from 'components/ListaTags/Tag'
import WordCloud from 'components/WordCloud/WordCloud'
import Polaridad from 'components/Polaridad/Polaridad'
import {
  dataPie,
  legendPie,
  dataSales,
  optionsSales,
  responsiveSales,
  legendSales,
  dataBar,
  optionsBar,
  responsiveBar,
  legendBar
} from "variables/Variables.jsx";
import { thisTypeAnnotation } from "@babel/types";

class Dashboard extends Component {


  state = {
    tagActual: 0,
    tweetWidth: 0,
    wordCloudWidth: 0,
    wordCloudHeight: 0
  }

  updateDimensions = () => {
    this.setState({ tweetWidth: this.contentRef ? this.contentRef.offsetWidth : 0 })
  };

  componentDidMount() {
    console.log("contentRef", this.contentRef)
    // this.setState({
    //   tweetWidth: this.contentRef ? this.contentRef.offsetWidth : 0
    //   // wordCloudWidth: this.RefwordContainer? this.RefwordContainer.offsetWidth: 0,
    //   // wordCloudHeight: this.RefwordContainer? this.RefwordContainer.offsetHeight: 0
    // })
    this.setState({
      dimensions: {
        width: this.container.offsetWidth,
        height: this.container.offsetHeight,
      },
    });
    window.addEventListener('resize', this.updateDimensions);
  }

  handleNextTag = () => {
    console.log('next')
    let { tagActual } = this.state;
    if (tagActual < (this.props.consulta.query.split(";").length - 1)) {
      console.log('si next')
      tagActual++;
      this.setState({ tagActual })
    }
  }

  handlePrevTag = () => {
    console.log('prev')
    let { tagActual } = this.state;
    if (tagActual > 0) {
      console.log('si prev')
      tagActual--;
      this.setState({ tagActual })
    }
  }

  createLegend(json) {
    var legend = [];
    for (var i = 0; i < json["names"].length; i++) {
      var type = "fa fa-circle text-" + json["types"][i];
      legend.push(<i className={type} key={i} />);
      legend.push(" ");
      legend.push(json["names"][i]);
    }
    return legend;
  }

  render() {
      const { dimensions } = this.state;
      return (
        <div className="content">
          <Grid fluid>
            <Row>
              <Col lg={3} sm={6}>
                <StatsCard
                  bigIcon={<div><i className="pe-7s-hourglass" /></div>}
                  statsText="Estado"
                  statsValue={<span style={{ fontSize: '18px' }}>Procesando</span>}
                  statsIcon={<i className="fa fa-refresh" />}
                  statsIconText="Actualizar ahora"
                />
              </Col>
              <Col lg={3} sm={6}>
                <StatsCard
                  bigIcon={<i className="pe-7s-ribbon text-success" />}
                  statsText="Tags"
                  statsValue={(this.props.consulta ? this.props.consulta.query ? this.props.consulta.query.split(";").length : 0 : 0)}
                  statsIcon={<i className="fa fa-calendar-o" />}
                  statsIconText={""}
                />
              </Col>
              <Col lg={3} sm={6}>
                <StatsCard
                  bigIcon={<i className="pe-7s-graph1 text-danger" />}
                  statsText="14/10/2019"
                  statsValue="23:12"
                  statsIcon={<i className="fa fa-clock-o" />}
                  statsIconText="Generar nueva versin"
                />
              </Col>
              <Col lg={3} sm={6}>
                <StatsCard
                  bigIcon={<i className="fa fa-twitter text-info" />}
                  statsText="Tweets"
                  statsValue="+4500"
                  statsIcon={<i className="fa fa-refresh" />}
                  statsIconText="Actualizar valor"
                />
              </Col>
            </Row>
            <Row>
              <Col md={8}>
                <Card
                  statsIcon="fa fa-check"
                  id="chartHours"
                  title=""
                  category=""
                  // ref={el => (this.RefwordContainer = el)}
                  stats="Espacio para WordCloud"
                  content={
                    <div style={{ overflow: 'scroll' }}>
                      {/* <WordCloud wordCloudHeight={this.state.wordCloudHeight} wordCloudWidth={this.state.wordCloudWidth} words={this.props.consulta && this.props.consulta.resultado ? this.props.consulta.resultado.word_cloud : []} /> */}
                      <WordCloud words={this.props.consulta && this.props.consulta.resultado ? this.props.consulta.resultado.word_cloud : []} />
                    </div>
                  }
                  legend={
                    <div className="legend"></div>
                    // <div className="legend">{this.createLegend(legendSales)}</div>
                  }
                />
              </Col>
              <Col md={4}>
                <Card
                  statsIcon="fa fa-clock-o"
                  title={<>Sentimiento <span><i className="fa fa-angle-left" onClick={this.handlePrevTag} style={{ cursor: 'pointer' }} /><Tag tagEditable={false} width={200} tag={this.props.consulta && this.props.consulta.resultado ? this.props.consulta.resultado.keywords_analysis[this.state.tagActual].name : 'load'} /><i className="fa fa-angle-right" onClick={this.handleNextTag} style={{ cursor: 'pointer' }} /></span> </>}
                  category="Anlisis general de polaridad"
                  stats="Campaign sent 2 days ago"
                  content={
                    <div
                      id="chartPreferences"
                      className="ct-chart ct-perfect-fourth"
                    >
                      <ChartistGraph data={{
                        labels: [this.props.consulta && this.props.consulta.resultado ? (this.props.consulta.resultado.keywords_analysis[this.state.tagActual].positive * 100).toFixed(0) + "%" : '', this.props.consulta && this.props.consulta.resultado ? (this.props.consulta.resultado.keywords_analysis[this.state.tagActual].negative * 100).toFixed(0) + "%" : ''],
                        series: [{
                          value: this.props.consulta && this.props.consulta.resultado ? this.props.consulta.resultado.keywords_analysis[this.state.tagActual].positive * 100 : 0,
                          className: 'xx1'
                        }, {
                          value: this.props.consulta && this.props.consulta.resultado ? this.props.consulta.resultado.keywords_analysis[this.state.tagActual].negative * 100 : 0,
                          className: 'xx2'
                        }]
                      }} type="Pie" />
                    </div>
                  }
                  legend={
                    <div className="legend">{this.createLegend(legendPie)}</div>
                  }
                />
              </Col>
            </Row>

            <Row>
              <Col md={6}>
                <Card
                  id="chartActivity"
                  title=""
                  category=""
                  stats="Espacio para otra cosa"
                  statsIcon="fa fa-check"
                  content={
                    <div>
                      <Polaridad />
                    </div>
                  }
                  legend={
                    <div></div>
                  }
                />
              </Col>

              <Col md={6} >
                <Card

                  title="Top 50"
                  category="Los 50 Tweets con ms retweets"
                  stats="Updated 3 minutes ago"
                  statsIcon="fa fa-history"
                  content={
                    <div className="table-full-width" style={{ height: '50vh', overflowX: 'hidden', overflowY: 'scroll' }} ref={el => (this.container = el)}>
                      <table className="table">
                        <Tasks tweetWidth={dimensions && dimensions.width} tweets={this.props.consulta && this.props.consulta.resultado ? this.props.consulta.resultado.retweets_analysis : []} />
                      </table>
                    </div>
                  }
                />
              </Col>
            </Row>
          </Grid>
        </div>
      );

  }
}

export default Dashboard;
