import React, { Component } from "react";
import { NavItem, Nav, NavDropdown, MenuItem } from "react-bootstrap";

class AdminNavbarLinks extends Component {
  render() {
    const versionControl = (
      <div>
        <i className="fa fa-history" />
        <span className="visible-lg-inline visible-md-inline"> consulta actual </span>
        <b className="caret" />
        <p className="hidden-lg hidden-md">consulta actual</p>
      </div>
    );
    const notification = (
      <div>
        <i className="fa fa-globe" />
        <b className="caret" />
        <span className="notification">6</span>
        <p className="hidden-lg hidden-md"> notificaciones</p>
      </div>
    );
    return (
      <div>
        <Nav>
          <NavDropdown
            eventKey={1}
            title={versionControl}
            noCaret
            id="basic-nav-dropdown"
          >
            <MenuItem eventKey={1.1} onClick={() => this.props.handleItem(0)}><i className="fa fa-plus" /> Crear consulta</MenuItem>
            <MenuItem divider />
            {this.props.consultas.map((d, index) => {
              return (
                <MenuItem className="dropVersions" disabled={d.status == 'waiting' || d.status == 'processing'} eventKey={index} key={index} onClick={() => { if (d.status === 'complete') { this.props.setConsultaActual(d.id) } }} style={{ paddingRight: '0', '& a': { paddingRight: '0' } }}>
                  <div style={{
                    overflow: 'hidden',
                    whiteSpace: 'nowrap'
                  }}>
                    <div style={{
                      display: 'inline-block'
                    }}>
                      {d.name || d.id}
                    </div>
                    <div style={{
                      display: 'inline-block',
                      float: 'right'
                    }}>
                      {
                        d.status === 'processing' ? <div className="lds-dual-ring"></div> : d.status === 'complete' ? <i className="CompletedIcon pe-7s-check" /> : <i className="WaitingIcon pe-7s-clock" />
                      }
                    </div>
                  </div>
                </MenuItem>)
            })}
          </NavDropdown>
        </Nav>
        {/* <Nav pullRight>
          <NavItem eventKey={3} href="#">
            Log out
          </NavItem>
        </Nav> */}
      </div>
    );
  }
}

export default AdminNavbarLinks;
